}

h2 {
    width: 100%;
    text-align: center;
    color: rgb(35, 59, 83);
    font-size: 36px;
    font-weight: normal;
    margin: 35px;
}

.user-info {
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 22.8px 1.2px rgba(0, 0, 0, 0.11);
    display: flex;
    padding: 40px;
    margin: 0 auto;
    align-items: flex-start;

    @media only screen and (max-width: 700px) {
        flex-direction: column;
        padding: 20px;
    }

    .img {
        width: 37%;
        margin-right: 30px;
        flex-shrink: 0;

        @media only screen and (max-width: 700px) {
            width: 100%;
            margin-right: 0;
            margin-bottom: 20px;
        }

        img {
            width: 100%;
        }
    }

    .data {
        display: flex;
        flex-direction: column;

        hr {
            display: none;

        }

        .text {
            margin-top: 37px;
            font-size: 15px;
            color: rgb(35, 59, 83);
        }

        .name {
            margin-top: 37px;
            font-size: 15px;
            font-weight: normal;
            color: rgb(35, 59, 83);
        }

